body {
  background-color: black;
}

img {
  display: block;
}

.search-form {
  display: flex;
  justify-content: center;
}

input {
  width: 200px;
  height: 30px;
  border-radius: 8px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  background-color: #ffffff;
  margin: 10px;
}

.stats {
  display: flex;
  justify-content: space-around;
  height: 50px;
}

#load-btn {
  padding: 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 40px;
  text-align: center;
  border-radius: 15px;
}

.hidden {
  visibility: hidden;
}
